<script>
	// import starV from '../../../components/rate-star/rate.vue'
	import starV from '../../../components/banxing/banStar.vue'
	export default {
		components: {
			starV
		},
		data() {
			return {
				windowHeight: 0,
				windowWidth: 0,
				statusBarHeight: 0,
				orderID: '',
				menuButtonWidth: 0,
				word:'',
				star:0,
				shopImglist:[]
			}
		},
		onLoad(option) {
			var res = uni.getSystemInfoSync()
			var that = this
			that.orderID = option.orderID
			that.windowHeight = res.windowHeight
			that.windowWidth = res.windowWidth
			that.statusBarHeight = getApp().globalData.top + 48
			that.menuButtonWidth = getApp().globalData.menuButtonWidth
			
		},
		methods: {
			doBack(){
				uni.navigateBack({
					
				})
			},
			onChange(e){
				console.log(e)
				this.star = e.value
			},
			doSubmit(){
				var that = this
				that.http.sendRequest('Orders.Evaluate',{
					'orderid':that.orderID,
					'star':that.star,
					'content':that.word
					}).then(function(res){
					if(res.code == 0){
						uni.navigateBack({
							
						})
					}
				})
			},
			input(e){
				this.word = e.detail.value
			},
			doDelateShopImage(index){
				this.shopImglist.splice(index,1)
			},
			doAddImage(index){
				var that = this
				if(index == 0){
					uni.chooseImage({
						count: 5 - that.shopImglist.length,
						sizeType: ['original', 'compressed'],
						sourceType: ['album', 'camera'],
						success: function(res) {
							let tempFilePaths = res.tempFilePaths;
							for (var i = 0; i < tempFilePaths.length; i++) {
								var path = tempFilePaths[i];
								if(that.shopImglist.length < 5){
									that.shopImglist.push(path)
								}
								
							}
							
						}
					})
				}
				
			}
		}
	}
</script>

